                p.mb-0 La implementacin de los proyectos productivos pecuarios es una de las actividades que se ha convertido en alternativa de empleo digno para miles de familias en el territorio nacional, ya que es una industria creciente en un pas que cada vez demanda ms alimentos y materias primas. 
              p.mb-0 Como muchas otras industrias en Colombia, la empresa pecuaria ha evolucionado en los ltimos aos y se ha transformado conforme a las exigencias normativas que rigen los temas de empleo, seguridad y formalizacin laboral. Igualmente, la adopcin por parte de muchos empresarios del agro de los lineamientos y principios de las Buenas Prcticas Pecuarias (BPP) y Buenas Prcticas Agrcolas (BPA) ha llevado a que se incorporen en la actividad productiva estos elementos, con lo cual <b style="color:#A3314F">el sector agropecuario ha dado un paso importante hacia la tecnificacin de los procesos y el desarrollo de la actividad productiva conforme a los estndares internacionales</b>. 
          p.mb-4 En cuanto a la normativa relacionada con la seguridad y salud en el trabajo se tiene que desde la dcada de los 50 se vienen dando avances importantes en lo relacionado con la reivindicacin de los derechos de los trabajadores y la bsqueda de relaciones ms justas entre estos y sus patronos. Con la incorporacin del Cdigo Sustantivo del Trabajo se logr aportar a la consolidacin de las relaciones entre trabajadores y empleadores dentro de un ambiente de coordinacin econmica y equilibrio social. 
