      #t_10_1.titulo-segundo.color-acento-contenido
        h2 10.1 Clases de riesgo

      .row.justify-content-center.mb-5(data-aos="fade-right")
        .col-lg-5.col-12
          p.mb-lg-0.mb-3 Para hablar de seguridad y salud en el trabajo obligatoriamente se debe hablar de riesgos, pues estos son inherentes a toda actividad humana. De acuerdo con el Ministerio de Trabajo, un riesgo se define <b style="color:#A3314F">como la combinacin de la probabilidad de que ocurra una o ms exposiciones o eventos peligrosos y la severidad del dao que puede ser causada por estos</b>. Por otro lado, la estimacin del riesgo consiste en emitir un juicio sobre la tolerancia o no del riesgo estimado. En este orden de ideas, la seguridad y salud en el trabajo es el desarrollo de un proceso lgico y por etapas, basado en la mejora continua, con el objetivo de anticipar, reconocer, evaluar y controlar los riesgos que puedan afectar la seguridad y salud en el trabajo (Ministerio de trabajo 2016).
        .col-lg-7.col-8
          img.mb-0(src="@/assets/curso/tema10/img04.png")

      .titulo-sexto.color-acento-contenido
        p.titulo <b>Tabla 11.</b> <i>Clasificacin de los riesgos laborales de acuerdo con las aseguradoras de riesgos laborales</i> 

