      p.mb-0 En relacin con la clasificacin de los riesgos, de acuerdo con la normatividad vigente se tiene que existen cinco (5) clases de riesgo, dependiendo de aspectos como las materias primas con las que se trabaja, los materiales, insumos que sean utilizados, medios de produccin, procesos, almacenamiento y transporte. Este aspecto es importante pues la definicin de la clase de riesgo indica la tarifa sobre la cual se har la cotizacin a la Aseguradora de Riesgos Laborales (ARL).   
