      h2 10.3 Medidas de proteccin

    p.mb-5 La identificacin de los riesgos es un paso fundamental para establecer las medidas de proteccin y los planes de mejoramiento para los diversos procesos que se desarrollan en la empresa pecuaria. En este punto es importante analizar el ciclo PHVA (planear, hacer, verificar y actuar) ya que muchas de las recomendaciones que se dan para el ejercicio con seguridad de las funciones al interior de la unidad productiva se desprenden de este ejercicio. Para la definicin de las medidas de proteccin frente a los peligros identificados en la unidad productiva es importante tener en cuenta la jerarqua de controles:
    .row.justify-content-center.mb-0(data-aos="fade-right")
        .col-lg-5.col-8       
