        .col-lg-7.col-12
          AcordionA.mb-0(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
            div(titulo="Eliminacin")
              p.mb-0 Realizar una modificacin profunda para eliminar un peligro: por ejemplo, adquirir un montacargas para evitar el esfuerzo fsico en el transporte de materias primas. 
            div(titulo="Sustitucin")
              p.mb-0 Por ejemplo, sustituir las guadaadoras por desbrozadoras autopropulsadas para hacer control de arvenses. 
            div(titulo="Controles de ingeniera")
              p.mb-0 Consiste en realizar modificaciones en las instalaciones para reducir los peligros a los que se expone el personal. Por ejemplo, instalar ventiladores ms potentes en el galpn para evitar golpes de calor. 
            div(titulo="Controles administrativos")
              p.mb-0 Son medidas que se toman desde la direccin de la empresa y se incorporan con la dinmica organizacional. Por ejemplo, jornadas de capacitacin en operacin de equipos. 
            div(titulo="Elementos de proteccin personal EPP")
              p.mb-0 Dotar a los trabajadores con elementos de proteccin para realizar las labores cotidianas, con lo que es posible prevenir accidentes.  
            
